import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {HeaderComponent} from './header/header.component';
import {WelcomeComponent} from './welcome/welcome.component';
import {TitleComponent} from './title/title.component';
import {ContactComponent} from './contact/contact.component';
import {AgrifarmerComponent} from './agrifarmer/agrifarmer.component';
import {AquafarmerComponent} from './aquafarmer/aquafarmer.component';

import {AgrimerchantComponent} from './agrimerchant/agrimerchant.component';
import {AquamerchantComponent} from './aquamerchant/aquamerchant.component';
import {SellComponent} from './sell/sell.component';
import {LoginComponent} from './login/login.component';
import {RegisterComponent} from './register/register.component';
import {SlideshowComponent} from './slideshow/slideshow.component';
import {MdetailsComponent} from './mdetails/mdetails.component';
import {MarketsComponent} from './markets/markets.component';
import {ProfileComponent} from './profile/profile.component';
import {DetailsComponent} from './details/details.component';
import {AdetailsComponent} from './adetails/adetails.component';
import {ArticlesComponent} from './articles/articles.component';
import {CropsComponent} from './crops/crops.component';
import {ProfileEditComponent} from './profile-edit/profile-edit.component';
import {RegistereComponent} from './registere/registere.component';


const routes: Routes =
 [
  {path:'header',component:HeaderComponent},
  {path:'',component:WelcomeComponent},
  {path:'title',component:TitleComponent},
  {path:'contact',component:ContactComponent},
  {path:'agrifarmer',component:AgrifarmerComponent},
  {path:'agrimerchant',component:AgrimerchantComponent},
  {path:'aquafarmer',component:AquafarmerComponent},
  {path:'aquamerchant',component:AquamerchantComponent},
  {path:"sell",component:SellComponent},
  {path:'login',component:LoginComponent},
  {path:'slides',component:SlideshowComponent},
  {path:'mdetails',component:MdetailsComponent},
  {path:'markets',component:MarketsComponent},
  {path:'profile',component:ProfileComponent},
  {path:'register',component:RegisterComponent},
  {path:'details',component:DetailsComponent},
  {path:'adetails',component:AdetailsComponent},
  {path:'articles',component:ArticlesComponent},
  {path:'crops',component:CropsComponent},
  {path:"profile-edit",component:ProfileEditComponent},
  {path:"edit-profile",component:RegistereComponent},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
